import { Component, OnInit } from "@angular/core";
import { LoginBody } from "../idealz/requests/login-body";
import { ApiService } from "../idealz/services/api/api.service";
import { Router } from "@angular/router";
import { LocalStorageService } from "angular-web-storage";
import { ToastrManager } from "ng6-toastr-notifications";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"]
})
export class LoginComponent implements OnInit {
  loginBody = new LoginBody();

  errorMessage: any = "Error";
  flags = {
    isLogin: false,
    isError: false
  };
  constructor(
    private api: ApiService,
    private localStorage: LocalStorageService,
    private router: Router,
    private toastr: ToastrManager
  ) { }

  ngOnInit() {
  }

  login() {
    this.api.singIn(this.loginBody).subscribe(res=>{
      if(res.statusCode==200){
      this.api.sendToken(res.data.accessToken)
      this.toastr.successToastr(res.message, '', {
        maxShown: 1
      });
      this.router.navigateByUrl("/dashboard");
}else{
  this.toastr.errorToastr(res.message, '', {
    maxShown: 1
  });
  this.loginBody.email="";
  this.loginBody.password="";
}
  })
  }
}
